<template>
  <div class="edit-container">
    <div class="header">
      <div class="inner-wrapper">
        <div class="header-area" @click="navToList()">
          <i class="el-icon-back mr10"></i>
          <!-- <span>问卷标题</span> -->
        </div>
        <div class="head-nav">
          <div class="header-list" :class="{'active': step == 'base'}" @click="navTo('base')">
            基础配置
            <div class="underLine" :class="{'active': step == 'base'}"></div>
          </div>
          <div class="space">
            <div class="separator"></div>
          </div>
          <div class="header-list" :class="{'active': step == 'question'}" @click="navTo('question')">
            编辑项目
            <div class="underLine" :class="{'active': step == 'question'}"></div>
          </div>
        </div>
        <div class="user">
          <span class="username">{{name}}</span>
          <i class="el-icon-switch-button" @click="logout"></i>
        </div>
      </div>
    </div>
    <router-view :key="$route.fullPath" class="mt64"></router-view>
  </div>
</template>

<script>
  import api from '~/api/api';
  import {mapState} from 'vuex';
  import store from '~/store/index';
  import * as types from '~/store/mutation-type';
  import {getStore} from '~/tools/utils';

  export default {
    computed: {
      ...mapState({
          step: state => state.step
      })
    },
    data() {
      return {
        name: getStore('name'),
        activeTab: 'my'
      };
    },
    mounted() {
      if(this.$route.params.sid) {
        this.navTo('question');
      } else {
        this.navTo('base');
      }
    },
    methods: {
      logout() {
        api.logout().then(res => {
          this.$router.push({ path: `/login` });
        })
      },
      navToList() {
        store.commit(types.CLEAR_SELECTED_ITEM);
        this.$router.push({ path: '/list/my' });
      },
      navTo(path) {
        if(path == 'question' && !this.$route.params.sid) {
          this.$alert('请先创建问卷', '提示', {
            confirmButtonText: '确定',
            callback: action => {
              this.dialogVisible = false;
            }
          });
          return;
        }
        if(this.$route.params.sid) {
          this.$router.push({ path: `/edit/${this.$route.params.sid}/${path}` });
        } else {
          this.$router.push({ path: `/edit/${path}` });
        }
        store.commit(types.SET_STEP, path);
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .edit-container {
    min-height: 100%;
    width: 100%;
    background #EEEFF1
    overflow: hidden;
    .header {
      position: fixed;
      -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
      box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
      background: #fff;
      font-size: 16px;
      -webkit-transition: all .4s ease;
      -o-transition: all .4s ease;
      transition: all .4s ease;
      height: 64px;
      display flex
      width 100%
      z-index 3
      .inner-wrapper {
        min-width 1306px
        width 100%
        margin 0 auto
        position relative
        .header-area {
          position absolute
          cursor pointer
          left 40px
          padding-top 22px
          .mr10 {
            margin-right 10px
          }
        }
        .head-nav {
          padding-top: 22px;
          position absolute
          left 50%
          margin-left -100px
          width 200px
          display flex
          .space {
            height 40px;
            width 80px
            display flex
            justify-content center
            padding-top 7px
            .separator{
              &:before {
                transform: rotate(-45deg) translateY(-4px);
                transform-origin: 0 0;
                content: "";
                display: inline-block;
                position: absolute;
                height: 15px;
                width: 1px;
                background: #c0c4cc;
              }
              &:after {
                transform: rotate(45deg) translateY(4px);
                transform-origin: 100% 100%;
                content: "";
                display: inline-block;
                position: absolute;
                height: 15px;
                width: 1px;
                background: #c0c4cc;
              }
            }
          }
          .header-list {
            float: left;
            font-size: 15px;
            padding-bottom: 16px;
            line-height: 24px;
            box-sizing border-box
            position relative
            &.active {
              color: #2672FF
            }
            .underLine {
              position absolute
              bottom -2px
              transition: all .3s ease;
              height: 2px;
              width 0px;
              background  #2672FF
              &.active {
                width 60px;
              }
            }
          }
        }
        .user {
          position absolute
          right 40px
          top 22px
          .username {
            margin-right 10px
          }
          i {
            &:hover {
              color red
            }
          }
        }
      }
    }
    .mt64 {
      margin-top 64px
    }
  }
</style>



